#about-me-component {
  display: flex;
  flex-direction: column;

  flex-grow: 1;
  height: 100%;

  width: 100%;

  p {
    margin: var(--medium_size_margin_em);
  }

  .about-me-content {
    overflow: auto;
    .interest-chip,
    .skill-chip,
    .language-chip,
    .academic-title-chip {
      margin: var(--medium_size_margin_em);
      background-color: #1591f035;
      color: #333;
      border-radius: var(--small_border_radius_px);
      font-size: 14px;
      font-weight: bolder;
    }

    .social-links {
      font-weight: bolder;

      display: flex;
      flex-grow: 1;

      a {
        text-decoration: none;
        color: black;
      }

      .social-icon-link {
        font-weight: bolder;
        margin: var(--medium_size_margin_em);
      }
    }
  }
}
