:root {
  --small_size_margin_em: 0.2em;
  --medium_size_margin_em: 0.4em;

  --small_size_padding_em: 0.2em;
  --medium_size_padding_em: 0.4em;

  --smaller_border_radius_px: 6px;
  --small_border_radius_px: 16px;
}

html,
body {
  height: 100%;
  width: 100%;

  margin: 0;
  padding: 0;

  user-select: none;
  overflow: hidden;

  background-color: #008cff4d;
}

body {
  font-family:
    -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;

  header {
    max-width: min-content;
    margin: var(--small_size_margin_em);
  }
  main {
    width: 100%;
    margin: var(--small_size_margin_em);
  }
}
