.jobs-component {
    display: flex;
    flex-direction: column;
    height: 100%;

    .jobs-list {
        overflow: auto;

        .one-job-card {
            background-color: #1591f035;

            margin: var(--medium_size_margin_em);
            padding: var(--medium_size_padding_em);

            border-radius: var(--small_border_radius_px);

            .position-chip,
            .skill-chip {
                margin: var(--medium_size_margin_em);

                background-color: rgba(195, 228, 255, 0.6);
                color: #333;
                border-radius: var(--small_border_radius_px);

                font-size: 14px;
                font-weight: bolder;
            }
        }
    }
}
