#projects-component {
    display: flex;
    flex-direction: column;

    flex-grow: 1;
    height: 100%;

    #projects-summary {
        overflow: auto;
        padding: 0 var(--medium_size_padding_em);
    }

    #component-wrapper {
        display: flex;
        flex-direction: column;
        overflow: auto;
    }

    #navigation-to-projects-wrapper {
        display: flex;
        flex-wrap: wrap;
        flex-grow: 1;

        justify-content: center;
        align-items: flex-end;
        align-content: end;

        margin: var(--medium_size_margin_em);

        button {
            flex-grow: 1;
            height: max-content;
            margin: var(--medium_size_margin_em);
            align-self: flex-end;
        }
    }

    /*  #projects-wrapper {
        flex-grow: 1;
        height: 100%;
        overflow: auto;

        background-color: red;
    }*/
}
